isGETime(start, time) {
if(time[0] < start[0]) return false;
if(time[0] > start[0]) return true;

if(time[1] < start[1]) return false;
if(time[1] > start[1]) return true;

return time[2] >= start[2];
},
isGTime(start, time) {
if(time[0] < start[0]) return false;
if(time[0] > start[0]) return true;

if(time[1] < start[1]) return false;
if(time[1] > start[1]) return true;

return time[2] > start[2];
},
zerolize(d) {
d += ''
if(d.length < 1) d = '0' + d;
return d;
},
itemStatusTxt(index) {
var gameStatus = this.itemStatus(index)
var name = '进行中'
if(gameStatus < 0) name = '已结束';
if(gameStatus > 0) {
var obj = this.gameObjArr[index]
name = this.zerolize(obj.from[1]) + '月' + this.zerolize(obj.from[2]) + '日';
}
return name;
},
itemStatus(index) {
var status = 1 
var gObj = this.gameObjArr[index]
var dt = new Date()
var y = dt.getFullYear()
var m = dt.getMonth() + 1
var d = dt.getDate()
var now = [y, m, d]
if(this.isGETime(gObj.from, now) && this.isGETime(now, gObj.to)) status = 0;
if(this.isGTime(gObj.to, now)) status = -1;

return status;
}

},
created() {
this.getWxConfig(() => {
report('春节活动落地页', '分享', '次数')
})


},
mounted() {

},
